import FlareIcon from "@mui/icons-material/Flare";
import GroupsIcon from "@mui/icons-material/Groups";
import styled from "styled-components";

const StyledVisionMission = styled.div`
  display: grid;

  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 1rem;
  padding: 6rem 0;
`;

const VM = styled.div`
  padding: 1rem;
  border: 1.8px solid ${(props) => props.theme.colors.primary};
  border-radius: 10px;
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: center;
  grid-template-columns: 1fr;
  grid-template-rows: 0.2fr 0.4fr 1.8fr;
  gap: 0.4rem;
`;

const VMIcon = styled.div`
  background-color: white;
  position: relative;
  top: -3.4rem;
  font-size: 3.4rem;
  font-weight: 600;
`;

const VMTitle = styled.div`
  position: relative;
  top: -1.2rem;
  font-size: 1.4rem;
  font-weight: 600;
`;

const VMContents = styled.div`
  line-height: 1.4rem;
  text-align: start;
  padding: 0 4rem;
  font-size: 1rem;
`;

function VisionMission() {
  return (
    <>
      <StyledVisionMission>
        <VM>
          <VMIcon>
            <FlareIcon sx={{ fontSize: "3.4rem", paddingRight: "1rem" }} />
            Vision
          </VMIcon>
          <VMTitle>  </VMTitle>
          <VMContents>
            ASK        .
                 .
          </VMContents>
        </VM>

        <VM>
          <VMIcon>
            <GroupsIcon sx={{ fontSize: "3.4rem", paddingRight: "1rem" }} />
            Mission
          </VMIcon>
          <VMTitle>   </VMTitle>
          <VMContents>
               ,     .
                .
          </VMContents>
        </VM>
      </StyledVisionMission>
    </>
  );
}

export default VisionMission;
