import Inovation from "../../img/innovation.png";
import Trust from "../../img/trust.png";
import Cooporate from "../../img/cooporate.png";
import Grow from "../../img/grow.png";
import styled from "styled-components";

const CoreValue = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem 8rem;
`;

const CoreValueTitle = styled.div`
  font-size: 3.4rem;
  padding: 2rem;
`;

const CoreValueASK = styled.span`
  font-size: 3rem;
  font-weight: bolder;
  color: ${(props) => props.theme.colors.primary};
`;

const CoreValueContainer = styled.div`
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr; /* 2 */
  grid-template-rows: 1fr 1fr; /* 2 */
  gap: 1.8rem;
`;

const CoreValueElement = styled.div`
  display: grid;
  justify-content: center;
  text-align: center;
  align-items: center;
  grid-template-columns: 1fr;
  grid-template-rows: 0.5fr 0.5fr 0.5fr;
  font-size: 1.2rem;
`;

const StyledIcon = styled.img`
  width: 120px;
  height: 120px;
  margin: 0 auto;
`;

const CoreValueElementTitle = styled.div`
  font-weight: bold;
  text-align: center;
`;

const CoreValueElementContent = styled.div``;

function CompanyCoreValue() {
  return (
    <>
      <CoreValue>
        <CoreValueTitle>
          <CoreValueASK>ASK</CoreValueASK>  
        </CoreValueTitle>
        <CoreValueContainer>
          <CoreValueElement>
            <StyledIcon src={Inovation} />
            <CoreValueElementTitle> (Innovation)</CoreValueElementTitle>
            <CoreValueElementContent>
               ,  .
            </CoreValueElementContent>
          </CoreValueElement>
          <CoreValueElement>
            <StyledIcon src={Trust} />
            <CoreValueElementTitle> (Trust)</CoreValueElementTitle>
            <CoreValueElementContent>
                  .
            </CoreValueElementContent>
          </CoreValueElement>
          <CoreValueElement>
            <StyledIcon src={Cooporate} />
            <CoreValueElementTitle> (Collaboration)</CoreValueElementTitle>
            <CoreValueElementContent>
                   .
            </CoreValueElementContent>
          </CoreValueElement>
          <CoreValueElement>
            <StyledIcon src={Grow} />
            <CoreValueElementTitle> (Growth)</CoreValueElementTitle>
            <CoreValueElementContent>
                 .
            </CoreValueElementContent>
          </CoreValueElement>
        </CoreValueContainer>
      </CoreValue>
    </>
  );
}

export default CompanyCoreValue;
